import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { TurfComponent } from './components/turf/turf.component';
import { AggregatesComponent } from './components/aggregates/aggregates.component';
import { StonePavingComponent } from './components/stone-paving/stone-paving.component';
import { SleepersComponent } from './components/sleepers/sleepers.component';
import { AreasWeDeliverToComponent } from './components/areas-we-deliver-to/areas-we-deliver-to.component';
import { ContactComponent } from './components/contact/contact.component';
import { FormrecComponent } from './components/formrec/formrec.component';

const routes: Routes = [
  {path : "", component : LoginComponent},
  {path : "login", component : LoginComponent},
  {path : "home", component : HomeComponent},
  {path : "turf", component : TurfComponent},
  {path : "aggregates", component : AggregatesComponent},
  {path : "stone-paving", component : StonePavingComponent},
  {path : "sleepers", component : SleepersComponent},
  {path : "areas-we-deliver-to", component : AreasWeDeliverToComponent},
  {path : "contact", component : ContactComponent},
  {path : "formrec", component : FormrecComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash : true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
